	module.exports = {"data":{"markdownRemark":{"html":"<p>Les commentaires permettent au code d’être ignoré. Dans le langage HTML, les commentaires sont faits en entourant le code que vous souhaitez ignorer avec <code>&#x3C;!--</code> et <code>--></code>.<br>\n<code>&#x3C;! - Ce texte n'apparaîtra pas puisqu'il est un commentaire -></code></p>\n<p>Vous pouvez facilement décommenter du code HTML en supprimant les éléments de commentaire.</p>\n<p><strong><em>Remarque:</em></strong> Soyez prudent quand vous décommentez du code HTML et pensez à bien enlever les éléments du début et de la fin de votre commentaire!</p>\n<p><strong><em>Note:</em></strong> Les commentaires peuvent être faits différemment dans d’autres langages de programmation, comme le JavaScript, mais l’idée générale d’enlever tout ce qui représente un commentaire s’applique.</p>","fields":{"slug":"/miscellaneous/challenge-dcommenter-html/"},"frontmatter":{"title":"Challenge Dcommenter HTML"}}},"pathContext":{"slug":"/miscellaneous/challenge-dcommenter-html/"}}

